.modal {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 21;
  transform: translate(-50%, -50%);
  transition: all 0.4s;
  &__dialog {
    width: 100%;
    background-color: #fff;
  }
  &__content {
    padding: 40px;
    text-align: center;
  }

  &__close {
    position: absolute;
    top: 5px;
    right: 15px;
    font-size: 40px;
    cursor: pointer;
    transition: color 0.4s;
    &:hover {
      color: red;
    }
  }
  &__title {
    color: var(--main-color);
    text-transform: uppercase;
  }
  &__text {
    margin-top: 10px;
  }
}
.overlay-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 20;
  display: none;
  transition: all 0.4s;
}
